/* Mobile Responsive XML Email Editor Template built by Adestra Digital Design - TP CP16964 03/2019 */
body {
  padding: 0;
  margin: 0;
}
body > table > td, body > table th {
  max-width: 600px;
}

a[x-apple-data-detectors] {
  color: inherit !important;
  text-decoration: none !important;
  font-size: inherit !important;
  font-family: inherit !important;
  font-weight: inherit !important;
  line-height: inherit !important;
}

body,
td {
  font-family: "Lato", verdana, arial, sans-serif;
  margin: 0px;
}

img {
  border: none;
  display: block;
  color: #000000;
}

.amf-editor .editor_only_dark_bg {
  background-color: #000000 !important;
}

.amf-editor:hover .spacer_target {
  min-height: 20px !important;
  display: block;
}

.button_alignment--align_button_left .align_target > table {
  float: left;
}

.button_alignment--align_button_center .align_target > table {
  float: none;
  margin: 0 auto;
}

.button_alignment--align_button_right .align_target > table {
  float: right;
}

.logo_alignment--align_logo_left .logo_container img {
  float: left;
}

.logo_alignment--align_logo_center .logo_container img {
  float: none;
  margin: 0 auto;
}

.logo_alignment--align_logo_right .logo_container img {
  float: right;
}

.readthisonline {
  background-color: #ffffff;
}

.readthisonline a {
  color: black;
  text-decoration: underline;
}

